@import '../utils';
.add-task-window{
    .task__window--hero{
    // background-color: bisque;
    position: relative;
    padding: 1rem 0;
    figure{
        position: absolute;
        top: 0;
        left: 0;
        
    }
    h3{
        width: 100%;
        text-align: center;
        font-size: 2rem;
        text-transform: capitalize;
    }
   }
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50rem;
    margin: auto;
    z-index: 105;
    background-color: $bg-color-box-taskes;
    padding: 3rem;
    box-shadow: 0 4rem 6rem rgba(0, 0, 0, 0.25);
    z-index: 1000;
    transition: all 0.5s;
    border-radius: 10px;
    .btn--close-modal {
        font-family: inherit;
        color: inherit;
        position: absolute;
        top: 0.5rem;
        right: 1.6rem;
        font-size: 3.5rem;
        cursor: pointer;
        border: none;
        background: none;
      }
    .upload__task{
        width: 80%;
        margin: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        place-content: center center;
        input{
            width: 100%;
            margin-bottom: 20px ;
            display: inline-block;
        }
        .input__task--name{
            margin: 20px auto;
        }
    }
   
}




