@import '../utils';
.grid-container{
    display: grid;
    grid-template-areas: 
    'title title title'
    'btnBox btnBox btnBox'
    'taskBox taskBox taskBox'
    ;
    width: 70%;
    background-color: $bg-color-box-taskes;
    gap: 10px;
    margin: auto;
    padding: 30px 110px;
    border-radius: 15px;
    position: relative;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
.main__title{
    grid-area: title;
   text-align: center;
   margin-bottom: 30px;
   h1{
    font-size: 5rem;
    letter-spacing: 1.5px;
    color: $color-title-;
    font-weight: bold;
    text-transform: capitalize;
   }
}
.btn__box{
    grid-area: btnBox;
    display: flex;
    justify-content: space-between;
    align-items: center;
   .select__box{
    outline: none;
    border: none;
    padding: 12px 24px;
    background-color: $bg-color-btn;
    border-radius: 6px;
    font-size: 2rem;   
    color: #fff;
    font-weight: 700;

   }
}
.taskes__box{
    grid-area: taskBox;
    background-color: $bg-color-taskes-box;
    padding: 5px 20px;
    border-radius: 15px;
    margin-bottom: 50px;
    .task__row{
      background-color: $bg-color-box-taskes;
      border-radius: 10px;
      padding: 10px;
      font-size: 1.2rem;
      position: relative;
      width: 100%;
      margin: 15px 0;
      .content__task{
        width: 95%;
        margin: auto;
      }
      .content__task{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .text{
           position: relative;
          .circle__logo{
            position: absolute;
            top: 7px;
            left: -23px;
            // background-color: $bg-color-taskes-box;
            width: 20px;
            height: 20px;
            border-radius:50%;
            display: flex;
            justify-content: space-around;
            align-items: center;
          }
          h3{
            text-transform: capitalize;
             margin: 5px;
             font-size: 2rem;
             color: $color-title-;
            }
           p.date{
            
            font-size: 1.5rem;
           }
          p.description{
            color: $color-text;
            width: 100%;
            line-height: 1.5;
            margin: 5px 0;
            font-size: 1.5rem;
          }
  
}
.icons{
  .icone__remove--task {
    .close{
      background-color: red;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      color: $color-text-white;
      font-weight: 600;
      font-size:2rem;
      cursor: pointer;
      display: flex;
      justify-content: space-around;
      align-items: center;
       border: none;
    }
  }
}
 } 
  }
}
}
