@import '../utils';
.login-box-height {grid-area: col;}
.login-box-width {grid-area: ro;}
.form__login--box{
  display: grid;
  grid-template-areas: 
  'col'
  'ro';
  width: 400px;
  margin: auto;
  background-color: $bg-color-box-taskes;
  padding: 40px 10px;
  row-gap: 40px;
  border-radius: 15px;

  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
.imgcontainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  
   img{
    position: absolute;
    top: 0px;
    left: 0px;
   }
    h1 {
        text-align: center;
        width: 100%;
        // background-color: aqua;
        text-transform: capitalize;
        padding-bottom: 5px;
        
      }   
    }
.form{
    width: 70%;
    margin: auto;
    display: grid;
    row-gap: 7px;
    label{
        text-transform: capitalize;
        letter-spacing: 1px;
        font-size: 14px;
    }
    input{
        @extend  .format-inputs;
    }
    .box-btn{
        // background-color: asqua;
        margin: 25px auto;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
}
}


