$bg-color:#cad2e0;
$color-title-:#6d727a;
$color-text:#383739;
$bg-color-btn:#528cc8;
$color-text-btn:#f5f5f5;
$bg-color-select-box:#bbb;
$bg-color-taskes-box:#bec2c33b;
$bg-color-box-taskes:#f5efef;
$bg-input-color:#fdfdfd;
$bg-color-btn-hover:#405cf5;
$color-text-white:#fff;
.normal-font{
    font-family: "Roboto", sans-serif;
    font-style: normal;
}
.roboto-thin {
     @extend .normal-font;
    font-weight: 100;
  }
  
  .roboto-light {
    @extend .normal-font;
    font-weight: 300;
   
  }
  
  .roboto-regular {
    @extend .normal-font;
    font-weight: 400;
   
  }
  
  .roboto-medium {
    font-weight: 500;
    @extend .normal-font;
  }
  .roboto-bold {
    @extend .normal-font;
    font-weight: 700;
  }
  .roboto-black {
    @extend .normal-font;
    font-weight: 900;
  }
  .italic-font{
    font-family: "Roboto", sans-serif;
    font-style: italic;
  }
  .roboto-thin-italic {
    @extend .italic-font;
    font-weight: 100;
    
  } 
  .roboto-light-italic {
    @extend .italic-font;
    font-weight: 300;
  } 
  .roboto-regular-italic {  
    font-weight: 400;
    @extend .italic-font;
  } 
  .roboto-medium-italic {
    font-weight: 500;
    @extend .italic-font;
  }
  .roboto-bold-italic {
  @extend .italic-font;
    font-weight: 700;
    font-style: italic;
  }
  .roboto-black-italic {
    @extend .italic-font;
    font-weight: 900;
  }
 .flex-column{
  display: flex;
  flex-direction: column;
  place-content: center center;
 }

 .format-inputs{
  outline: none;
  border: none;
  padding: 10px  8px;
  background-color:$bg-input-color;
  border-radius: 7px;
  border-bottom:2px solid transparent;
  transition: all 0.5s; 
  margin:auto;
  width: 100%;
  &::placeholder{
    opacity: 0.6;
    text-transform: capitalize;
    color: $color-text;
  }
}
 .format-inputs:focus{
  border-bottom: 2px solid $bg-color-btn;
 }

 

 .btn {
  background-image: linear-gradient(to right, #457fca 0%, #5691c8  51%, #457fca  100%);
  appearance: button;
  backface-visibility: hidden;
  background-color: $bg-color-btn;
  border-radius: 6px;
  border-width: 0;
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .1) 0 2px 5px 0, rgba(0, 0, 0, .07) 0 1px 1px 0;
  box-sizing: border-box;
  color: $color-text-btn;
  cursor: pointer;
  font-family: -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue", Ubuntu, sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.15;
  outline: none;
  overflow: hidden;
  padding: 15px 30px;
  letter-spacing: 1px;
  position: relative;
  text-align: center;
  text-transform: capitalize;
  transform: translateZ(0);
  transition: all .8s, box-shadow .08s ease-in;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  background-size: 200% auto;
  svg {
    height: 2.25rem;
    width: 2.25rem;
    fill: currentColor;
   
  }
}

.btn:hover {
  background-position: right center; /* change the direction of the change here */
  border: 2px transparent;
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(4px);
  z-index: 100;
  transition: all 0.5s;
}


.hidden {
  visibility: hidden;
  opacity: 0;
}


.completed{
  text-decoration: line-through;
}

.logo__checked{
  background-color: green;
}



.spinner {
  margin: 5rem auto;
  text-align: center;

  svg {
    height: 6rem;
    width: 6rem;
    fill: $bg-color-btn;
    animation: rotate 2s infinite linear;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}




         

