@import url('https://fonts.googleapis.com/css2?family=Acme&family=Open+Sans:wght@500&family=Oswald:wght@200;300;500;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import 'utils';
* {
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  html {
    box-sizing: border-box;
    font-size: 62.5%;
  }
 
  ul{
    list-style: none;
  }

  
  